import Vue from 'vue'
import VueResource from 'vue-resource';
import VueRouter from 'vue-router'  
import Vuex from 'vuex'

Vue.use(Vuex)
Vue.use(VueRouter);
Vue.use(VueResource );
// 导入 pages 下的 Home.vue
import login from './pages/login.vue'
import home from './pages/mains.vue'
const routes = [  
    {  
        path: '/',  
        component: login  
    },  
    {  
        path: '/home',  
        component: home  
    }  
]
const router = new VueRouter({  
    routes  
})  

import { chats } from './assets/js/chat'
window.chats = chats.connect() ; // 初始化连接  

new Vue({  
  el: '#app',  
  data(){  
    return {  
    	test:'test',
        transitionName: 'slide'
    }  
  },  
  router, // 在vue实例配置中，用router  
  watch: {  
    // 监视路由，参数为要目标路由和当前页面的路由  
    '$route' (to, from){  
        const toDepth = to.path.substring(0, to.path.length-2).split('/').length  
        // 官方给出的例子为 const toDepth = to.path.split('/').length 由于现在只有两个路由路径'/'和'/detail'  
        // 按照官方给的例子，这两个路由路径深度都为 2 ，所以，这里稍作调整，不知道有什么不妥  
        // 但目前在这个demo中能正常运行，如果知道更好的方法，欢迎留言赐教  
        const fromDepth = from.path.substring(0, from.path.length-2).split('/').length  
        this.transitionName = toDepth < fromDepth ? 'slide_back' : 'slide'  
        // 根据路由深度，来判断是该从右侧进入还是该从左侧进入  
    }  
  } ,
  created () {
  }
})  


