<template>
	<header>
		<div class="menu" v-on:click="showMenu"><i class="iconfont">&#xe63d;</i></div>
		<div class="headerTitle"><!--<img src="../../dist/logo.png"/>-->{{homename}}</div>
		<div class="menu" v-on:click="showNick"><i class="iconfont">&#xe621;</i></div>
	</header>
</template>
<style>
	header{
		width: 100%;
		height: 55px;
		line-height: 55px;
		position: relative;
		display: flex;
		z-index: 99;
	}
	.headerTitle{
		flex: 1;
		text-align: left;
		display: flex;
		vertical-align: middle;
		font-weight: 600;
	}
	.headerTitle img{
		display: inline;
		width: 40px;
		height: 40px;
	}
	.menu{
		margin: 11px;
		line-height: 44px;
		width: 50px;
		display: flex;
		justify-content: center;
		flex-direction: column;
	}
</style>
<script>
export default {
	data () {
		return {}
	},
	props:['menu','homename','showMenu','showNick']
}
</script>


