import io from 'socket.io-client'
var chats = {
	io:null,
	disconnect () {  // 关闭连接
		this.io.disconnect();
		return this
	},
	connect () { // 启动连接
		this.io = io('/',{'force new connection': true,'reconnect':true});
		this.init();
		return this
	},
	init:function(){
		this.io.on('connect', function() {
			console.log('用户已连接')
		});
		this.io.on('disconnect', function() {
			console.log('用户已断开')
		});
		return this
	},
	// sign 用来处理登录时的回调
	sign:{
		fn:function(){
			chats.io.on('register', (test)=>{
				if(test.msg == 200){
					this.toggle = 1;
				}else{
					alert(test.content)
				}
			})
			chats.io.on('signUp', (test)=>{
				if(test.msg == 200){
					localStorage['token'] = test.token
					window.location.hash='/home'
				}else{
					alert(test.content)
				}
			})
			return chats // 将chats返回出去  为了更好的回调一些别的用法
		},
		btn:{
			register (obj) {
				chats.io.emit('register',obj)
			},
			signUp (obj) {
				chats.io.emit('signUp',obj)
			}
		}
	},
	chat:{
		fn:function(){
			// 房间退出消息
			chats.io.on('logout',(test)=>{
				window.localStorage['token'] = '';
				window.location.hash = '/';
				chats.io.disconnect()
			});
				// 房间登录信息
			chats.io.on('login',(test)=>{
				if(test.msg){
					if(test.msg =='200'){
						alert(test.content)
					}
					chats.io.emit('logout')
					return
				}
				this.user.name = test.name;
				this.homename = test.roomid ;
				this.homes.push({
					name:test.roomid
				})
				this.chat.chat.btn.join({
		          	name:test.name,
		          	roomid:test.roomid
		        });
			});
			// 房间离开处理
			chats.io.on('leave',(test)=>{
				this.msg.push(test)
				this.$nextTick(scroll)
			});
			// 房间消息处理
			chats.io.on('msg',(test)=>{
				if(test.type){}else if(test.name == this.user.name){test.type = 'me'}else{test.type = 'users'}
				this.msg.push(test)
			});
			// 加入房间时触发
			chats.io.on('join',(test)=>{
				this.msg = [];
				let i = 0,msg=test,l=test.length;
				for(i;i!=l;i++){
					if(msg[i].type){}else if(msg[i].name == this.user.name){msg[i].type = 'me'}else{msg[i].type = 'users'}
					this.msg.push(msg[i])
				}
			});
			return chats
		},
		btn : {
			login () {
				let token = window.localStorage.token;
				chats.io.emit('login',{token : token});
				return chats
			},
			join (obj) {
				chats.io.emit('join',obj);
				return chats
			},
			msg (msg) {
				chats.io.emit('msg',msg);
				return chats
			},
			logout () {
				chats.io.emit('logout')
			}
		}
	}
}

function scroll (bool) {
	var dom = document.getElementsByClassName('main').item(0)
	if(dom||dom==null) return
	var wh = dom.clientHeight;
	var h = dom.scrollHeight;
	var top = dom.scrollTop;
	var sh = h - wh;
	// 消息来了的滚动条判断函数
	if(bool){
		dom.scrollTop = top;
	}else if(sh-top<200){
		dom.scrollTop = sh;
	}
}
export { chats }



