<template>
	<div class="sign">
		<div class="sign-navs">
			<div class="navs-slider">
				<a v-for="(v,t) in navs" :class="{active:toggle==t}" v-on:click="toggle=t">{{v.title}}</a>
				<span v-bind:style='navsSlider'></span>
			</div>
		</div>
		<div class="sign-box">
			<div class="inputs" v-for="(v,t) in navs[toggle].inputs">
				<input type="text" :placeholder="v.p" name="v.name" v-if="v.type==='text'" v-model='v.txt'/>
				<input type="password" :placeholder="v.p" name="password" v-model='v.txt' v-else-if="v.type==='password'" />
				<input type="file" :placeholder="v.p" name="nickname"  v-else/>
			</div>
		</div>
		<input type="submit" :value="navs[toggle].title" class="sign-submit" v-on:click="gohome"/>
	</div>
</template>
<style type="text/css" scoped>
.sign-navs{
	font-size: 18px;
	text-align: center;
    line-height: 35px;
    color: #555;
    margin-bottom: 10px;
}
.navs-slider{
	margin:  0 auto;
	display: flex;
	width: 200px;
	position: relative;
}
.navs-slider a{
    flex: 1;
}
.navs-slider span{
	flex: 1;
	position: absolute;
	width: 30%;
	height: 2px;
	margin: 0 10% ;
	left: 0;
	bottom: 0;
	background: #0F88EB;
	transition: all 1s;
}
.navs-slider .active{
    color: #0f88eb;
}
.sign-box{
    border: 1px solid #d5d5d5;
    border-radius: 3px;
}
.inputs{
	display: flex;
}
.inputs input{
	outline: none;
	border: 0;
	flex: 1;
	font-size: 14px;
	line-height: 44px;
	height: 44px;
	padding: 0 20px;
}
.inputs+.inputs{
    border-top: 1px solid #e8e8e8;
}
.sign-submit{
	width: 100%;
	border: 0;
	background: #0F88EB;
	color: #fff;
	padding: 10px 0;
	border-radius: 5px;
	margin-top: 18px;
    font-size: 14px;
    font-weight: 600;
}
</style>
<script type="text/javascript">
let navs = [
   		{
  	      title:'注册',
  	      inputs:[
  	        {
  	          p:'帐号',
  	          name:'username',
  	          type:'text',
  	          txt:''
  	        },
  	        {
  	          p:'密码',
  	          name:'username',
  	          type:'password',
  	          txt:''
  	        },
  	        {
  	          p:'昵称',
  	          name:'nickname',
  	          type:'text',
  	          txt:''
  	        }
  	      ]
  	    },
  	    {
  	      title:'登录',
  	      inputs:[
  	        {
  	          p:'帐号',
  	          name:'username',
  	          type:'text',
  	          txt:''
  	        },
  	        {
  	          p:'密码',
  	          name:'username',
  	          type:'password',
  	          txt:''
  	        }
  	      ]
  	    }
  	  ]
function trans() {
	var test = {
			transform: 'translateX('+(this.toggle*200/navs.length)+'px)'
  	}
  	return test
}
export default {
  data () {
  	return {
      toggle:0,
  	  navs:navs,
  	  chat: null
  	}
  },
  methods:{
  	gohome:function(){
  		var sign = {
  				name : navs[0].inputs[0].txt,
  				password : navs[0].inputs[1].txt,
  				nickname : navs[0].inputs[2].txt
  		} 
		var login = {
			name : navs[1].inputs[0].txt,
			password : navs[1].inputs[1].txt
		} 	
  		if(this.toggle==0){
  			window.chats.sign.btn.register(sign)
	  	}else{
  			window.chats.sign.btn.signUp(login)
	    }
  	}
  },
  created () {
	window.chats.disconnect().connect().sign.fn.call(this)
  },
  computed: {
     navsSlider:trans
  }
}
</script>


