<template>
	<div class="chat-box">
	<div v-bind:class="['chat',{chatShow:menu}]" >
		<headers :homename="homename" :menu="menu" :showMenu='showMenu' :showNick="showNick"></headers>
		<mains :msg='msg'></mains>
		<div v-bind:class="['footer']">
			<div>
				<div class="iconBox">
					<i class="iconfont" v-on:click='showEmoji'>&#xe78a;</i>
				</div>
			</div>
			<div class="footerCent">
				<div class="inputs">
					<input type="text" @keyup.enter="submit"  v-model="txt" ref="input"/>
				</div>
			</div>
			<div>
				<div class="iconBox" v-on:click="submit" >
					<i class="iconfont" >&#xe650;</i>
				</div>
			</div>
		</div>
		<transition name="fade">
			<div v-bind:class="'share'" v-show="menu" v-on:click="showMenu"></div>
		</transition>
	</div>
	 <transition name="fade">
		<brow v-if="emoji" v-on:click='showEmoji' :showEmoji='showEmoji'></brow>
 	 </transition>
	<menus :homes='homes' :user="user" :menu="menu" :homename='homename' :gohome='gohome' :showCreate='showCreate' :logout='logout' ref='menu'></menus>
	<createroom v-if="create" :showCreate="showCreate" :createRoomid="createRoomid"></createroom>
	</div>
</template>
<style type="text/css" scoped="scoped">
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.flex{
	flex: 1;
}
.chat-box{
	margin: 0 auto;
	height: 100%;
	overflow: hidden;
	position: relative;
	flex-direction: row-reverse;
	 -webkit-user-select:none; -moz-user-select:none; -ms-user-select:none; user-select:none;
}
.chat{
    background-color: rgb(245, 245, 245);
	background-image:url(http://www.fetchs.cn/images/kiana.jpg) ;
	background-size: cover;
    background-position: center center;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	height: 100%;
	position: absolute;
	transition: all .5s; 
	width: 100%;
	position : relative
}
/*.menuSlider,.menuOpen,.chat,.chatShow{
	transition-duration:.5s;
}*/
.menuSlider{
	position: absolute;
	display: flex;
	bottom: 0;
	height: 100%;
	width: 280px;
	z-index: 101;
	transform: translateX(-280px);
	transition: all .5s; 
	flex-direction: column;
}
.share{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.2);
	z-index: 100;
}
.chatShow{
	transform: translateX(280px);
}
.menuOpen{
	transform: translateX(0px);
}
@media screen and (max-width: 980px){
}
@media screen and (min-width: 981px){
}

.footer{
	height: 50px;
	display: flex;
	position : relative;
}
.footer .iconBox{
	height: 48px;
	width: 40px;
	line-height: 48px;
	text-align: center;
    font-weight: 600;
    font-size: 18px;
    color: #555;
    cursor: pointer;
}
.footerCent{
	flex: 1;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.footer .inputs{
	border-radius: 5px;
	background: #fff;
	margin: 2px;
	display: flex;
	height: 28px;
	line-height: 28px;
}
.footer .inputs input{
	outline: none;
	border: 0;
	flex: 1;
}
</style>
<script type="text/javascript">
import headers from '../components/headers.vue'
import mains from '../components/main.vue'
import menus from '../components/menu.vue'
import createroom from '../components/createRoom.vue'
import brow from '../components/brow.vue'
import BScroll from 'better-scroll'
import {chats} from '../assets/js/chat'
var user={
	name:null,
	saysay:'asdasdads'
}
var homes = [{name:1}] // 初始化给一个房间
var homename = 1;  // 初始化房间的编号
var msg =[]; // 初始化聊天记录
var menu = false;  // 菜单栏初始化
var notice = false;// 公告显示的参数
var create = false;
export default {
	data () {
		return {
			create : create , // 这个是否弹出显示创建房间的
			test : 'test' , // 这个属性只用来做测试
			menu : menu, // 切换显示菜单栏
			notice : notice,  // 公告切换
			chat : null, // io 的参数
			txt : '', // 发送消息的 input 参数
			msg : msg,  // 会话集合
			user : user,  // 人物信息
			homes : [],  // 房间集合
			homename : homename, // 当前房间在哪
			emoji : false  // 表情组件切换
		}
	},
	watch:{
		homename:function(){
			this.msg=[];
			this.chat.chat.btn.join({
	          	name:this.user.name,
	          	roomid:this.homename
	        })
		}
	},
	computed :{
		addHome:function(){
			let i=0,l=this.homes,arr=[];
			for (i in l){
				if(Object.prototype.toString.call(l[i].name)=='[object Number]'){
					l[i].name+=''
				}
				arr.push(l[i].name)
			}
			return arr
		}
	},
	methods:{
		showEmoji:function(str){
			if(Object.prototype.toString.call(str)=='[object String]'){
				this.txt +='!['+str+']'
			}
			this.emoji = !this.emoji;
			this.$refs.input.focus()
		},
		showMenu:function(){  // 是否显示菜单栏
			this.menu=!this.menu;
		},
		showCreate:function(e){ // 是否显示创建模块
			this.create = !this.create;
		},
		createRoomid:function(str){  // 添加房间逻辑处理
			if(str == this.homename){
				return
			}else if(this.addHome.indexOf(str)<0){
				this.homes.push({
					name: str
				});
			}
			this.$nextTick(()=>{
				this.homename = str;
			})
		},
		showNick:function(){  // 是否显示公告栏
			this.notice = !this.notice
		},
		// 进入某个房间
		gohome:function(name){
			// 将房间名称改变  并且与 io 通信 告知这个人 加入了房间； 这时信息将清空
			this.homename = name;
		},
		logout:function(){
			this.chat.chat.btn.logout()
		},
		submit: function(){
			if(this.txt==''){return}
			this.$refs.input.focus()
			var txt = this.txt;
			console.log(txt);
			var newTxt = txt.replace(/</,'\\<'); // 转义这个<
				newTxt = newTxt.replace(/>/,'\\>'); // 转义这个>
				newTxt = newTxt.replace(/\!\[(.+?)\]/,'<img src="http://www.fetchs.cn/images/emoji/$1.png" alt="$1">'); // 匹配替换成文字
			var name = this.user.name;
			this.$nextTick(() => {
		        this.chat.chat.btn.msg({
					name : name,
					msg: newTxt
				});
		   });
			this.txt='';
		},
		look : function(){

		}
	},
	components:{
		headers,  // 头部模块
		mains,  // 身体模块
		menus,  // 菜单模块
		createroom,  // 创建房间模块
		brow
	},
	props:['local'],
	created () {
		this.msg = []; // 清空消息记录  这样可以防止退出的时候  消息没有清空的问题
		this.chat = window.chats.disconnect()
								.connect().chat.fn.call(this)
								.chat.btn.login()
	}
}
</script>


